import { Component } from "react";
import '../css/header.css'; 
import $ from 'jquery';

class Header extends Component {

    componentDidMount() {
        if($('header').attr('class') !== 'gnbOn') {
            $('.logo').attr('src','src/img/logo/logo01.svg')
        } else {
        }
        $('.hamBtn').click(function() {
            $('#gnb').slideToggle();
            $('header').toggleClass('gnbOn');
            if($('header').attr('class') !== 'gnbOn') {
                $('.logo').attr('src','src/img/logo/logo01.svg');
            } else {
                $('.logo').attr('src','src/img/logo/logo02.svg');
            }
            $(this).toggleClass('xi-bars xi-close');
        });

    }
    render() {
        return(
            <header>
                <div className='container'>
                    <div className='row'>
                        <h1>
                            <a href='/'>
                                <img className='logo' src='src/img/logo/logo01.svg' alt='BK Partners Logo'></img>
                            </a>
                        </h1>
                        <p className='hamBtn xi-bars'></p>
                    </div>
                </div>
                <div id='gnb'>
                    <ul className='row'>
                        <li>
                            <a href='/'>
                                전월세보증금대출
                            </a>
                        </li>
                        <li>
                            <a href='/'>
                                자동차대출
                            </a>
                        </li>
                        <li>
                            <a href='/'>
                                부동산담보대출
                            </a>
                        </li>
                        <li>
                            <a href='/'>
                                개인신용대출
                            </a>
                        </li>
                        <li>
                            <a href='/'>
                                회생 / 회복 / 파산자 대출
                            </a>
                        </li>
                    </ul>
                </div>
            </header>
        )
    }
}

export default Header;